import ProductDetail from "@/app/product/ProductDetail";
import { getData } from "@/services/ProductService";
import Header from "@/components/header/Header";
import { fetchGroupedCategories } from "@/services/HomeService";
import ProductRelate from "@/app/product/ProductRelates";
import { notFound } from "next/navigation";

export default async function ProductPage({ params }) {
    const { slug } = params;

    const data = await getData(slug);

    // Nếu không có product thì trả về trang 404
    if (!data?.product) {
        notFound(); // Gọi đến trang not-found.tsx nếu có, hoặc mặc định của Next.js
    }

    const groups = await fetchGroupedCategories();

    return (
        <div>
            <Header groups={groups} />
            <ProductDetail product={data.product} />
            <ProductRelate products={data.productRelates} />
        </div>
    );
}
